.box{
    margin: 20px auto;
    padding: 10px;
    width: 500px;
    border: 1px solid gray;
    box-shadow: 0 0 10px gray;
    border-radius: 15px;
}
.section-box{
    padding: 10px 0;
    border-top: 1px solid gray;
    /* border-bottom: 1px solid gray; */
}
.title{
    margin-bottom: 10px;
}
.ipt{
    margin: 0 10px;
    border: 1px solid blue;
    height: 20px;
}
.add{
    display: inline-block;
    margin-right: 10px;
    padding: 5px 10px;
    background-color: chartreuse;
}
.list{
}

.list li{
    list-style-position: inside;
    margin-bottom: 10px;
}
.del-btn{
    display: inline-block;
    padding: 2px 5px;
    background-color: rgb(128, 163, 229);
    margin-left: 10px;
    color: #000;
}

.show-text{
    color: green;
    /* transition:all 0.5s ease-in; */
    /* forwords 执行一次 */
    animation: show 1.5s ease-in forwards; 
}
.hide-text{
    /* color: red; */
    animation: hide 1.5s ease-in forwards;
}
@keyframes show{
    0%{
        opacity: 0;
        color: pink;
    }
    50%{
        opacity: 0.5;
        color: blue;
    }
    100%{
        opacity: 1;
        color: blueviolet;
    }
}

@keyframes hide{
    0%{
        opacity: 1;
        color: pink;
    }
    50%{
        opacity: 0.5;
        color: blue;
    }
    100%{
        opacity: 0;
        color: blueviolet;
    }
}